import React from 'react';
import { sortableContainer } from 'react-sortable-hoc';
import SortableElement from './ListItem';

const SortableContainer = sortableContainer(({ load, setLoad, sortedTasks, setCurrentTask, setIsAddingOrIsUpdating }) => {
    const currentUser = JSON.parse(localStorage.getItem('user'));

    return (
        <ul className="App_list">
            {sortedTasks.map((task, index) => (
                task.creator === currentUser._id ?
                    <SortableElement 
                        key={`task-${task.text}`}
                        index={index}
                        task={task}
                        load={load}
                        setLoad={setLoad}
                        sortedTasks={sortedTasks}
                        setCurrentTask={setCurrentTask}
                        setIsAddingOrIsUpdating={setIsAddingOrIsUpdating}
                    /> : null
            ))}
        </ul>
    )
});

export default SortableContainer;
