import React from 'react';
import axios from 'axios';
import { sortableElement, sortableHandle } from 'react-sortable-hoc';

const DragHandle = sortableHandle(() => <span>::</span>);
const SortableElement = sortableElement(({ load, setLoad, task, sortedTasks, setCurrentTask, setIsAddingOrIsUpdating }) => {
    function deleteTask (id) {
        axios.delete(`http://localhost:8000/api/delete-task/${id}`)
            .then(res => console.log(res.data))
            .catch(err => console.log(err));
    }
    
    return (
        <li className="App_list_item">
            <div className="dragHelper">
                <div className="main">
                    <DragHandle />
                    
                    <div className="task-group">
                        <input
                            type="checkbox"
                            checked={task.completed}
                            onChange={() => {/* setsortedTasks(togglesortedTasks(sortedTasks, task)) */}}
                        />
                        <p className={`App_copy ${task.completed && 'lineThrough'}`}>{task.text}</p>
                    </div>

                    {/* Task action buttons (edit & delete) */}
                    <ul className="menu">
                        {!task.completed && (
                            <li className="menu-item">
                                <i
                                    className="fas fa-edit"
                                    onClick={() => {
                                        setCurrentTask(task);
                                        setIsAddingOrIsUpdating(true);
                                        setLoad(true);
                                    }}></i>
                            </li>
                        )}

                        <li className="menu-item">
                            <i 
                                className="fas fa-trash" 
                                onClick={() => {
                                    deleteTask(task._id);
                                    setLoad(true);
                                }}
                            ></i>
                        </li>
                    </ul>
                </div>

                {/* Task statusbar holding the creation and upadating timestamps */}
                {!task.completed ? (
                    <div className="status-bar">
                        <div className="timestamp-created">
                            <p>created on
                            <span> {task.createdDateAndTime.date} </span> at
                                <span> {task.createdDateAndTime.time}</span>
                            </p>
                        </div>

                        {/* <div className="timestamp-updated">
                            <p>last updated on
                            <span> {task.createdDateAndTime.date} </span> at
                                <span> {task.createdDateAndTime.time}</span>
                            </p>
                        </div> */}
                    </div>
                ) : null}

                {/* Task divider */}
                {sortedTasks.indexOf(task) < sortedTasks.length - 1 && (
                    <div className="divider">
                        <div className="dark"></div>
                        <div className="light"></div>
                    </div>
                )}
            </div>
        </li>
    );
});

export default SortableElement;
